          animation-delay: 4s;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity: 0;
  font-size: 7.5vw;
  font-family: 'HKGrotesk-Bold';
  letter-spacing: 0.2vw;
}

@-webkit-keyframes appear {
    from {opacity: 0;}
    to {opacity: 1;}
}

